import { Container, makeStyles, Typography } from "@material-ui/core";
import Carousel from "./Carousel";
import countapi from 'countapi-js';
import {useState, useEffect} from 'react';

const useStyles = makeStyles((theme) => ({
  banner: {
    //backgroundImage: "url(./banner2.jpg)",
  },
  bannerContent: {
    height: 400,
    display: "flex",
    flexDirection: "column",
    paddingTop: 25,
    justifyContent: "space-around",
  },
  tagline: {
    display: "flex",
    height: "40%",
    flexDirection: "column",
    justifyContent: "center",
    textAlign: "center",
  },
  carousel: {
    height: "50%",
    display: "flex",
    alignItems: "center",
  },
}));

function Banner() {
  const classes = useStyles();
  const [visits, setVisits] = useState(0);

  function countVisitors(){
    countapi.visits("page", "pierovicenti.github.io/CryptoShow").then((result) => {
      setVisits(result.value);
      console.log(result.value);
  });
  }

useEffect(()=>{
  countVisitors();
},[]);

  return (
    <div className={classes.banner}>
      <Container className={classes.bannerContent}>
        <div className={classes.tagline}>
          <Typography
            variant="h2"
            style={{
              fontWeight: "bold",
              marginBottom: 15,
              fontFamily: "Montserrat",
            }}
          >
            CryptoMania
          </Typography>
          <Typography
            variant="subtitle2"
            style={{
              color: "darkgrey",
              textTransform: "capitalize",
              fontFamily: "Montserrat",
            }}
          >
            Get all the Info regarding your favorite Crypto Currency
          </Typography>
          <Typography
            variant="subtitle2"
            style={{
              color: "darkgrey",
              textTransform: "capitalize",
              fontFamily: "Montserrat",
            }}
          >
          </Typography>
          <Typography variant="subtitle2" style={{color: "darkgrey", textTransform: "capitalize", fontFamily: "Montserrat"}}>Website Visited {visits} times</Typography>
        </div>
        <Carousel />
      </Container>
    </div>
  );
}

export default Banner;